.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100vh; 
}

button, .link-button {
  background-color: transparent; 
  border: 1px solid rgba(255, 255, 255, 0.7); 
  color: rgba(255, 255, 255, 0.7);
}

.form-button {
  margin: 5px; 
  padding: 5px 10px;
}
.basics-div {
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.basic-input {
  min-width: 200px;
  padding: 6px;
  margin: 5px 
}

.basic-label {
  min-width: 200px;
  margin: 5px; 
}
.landingpage-buttons {
  display: flex;
  justify-content: center;
  position: relative;
  align-items: center;
  z-index: 3;  
  height: 100vh;
}

.link-button {
  margin: 10px; 
  padding: 10px 20px;
  display: inline-block;
  font-size: 18px; 
  text-decoration: none;
}

.submit-buttons {
  padding: 5px 10px;
  font-size: 18px; 
  text-decoration: none;
}

h2 {
  z-index: 4; 
  position: fixed;
  left: 50px; 
  bottom: 70px;  
  color: white;
}

.header {
  display: flex;
  justify-content: center;
  z-index: 3;
  height: 0px;
}
.instructions p {
  font-size: x-small; 
}

h3 {
  z-index: 2; 
  position: relative; 
  color: white;
}
.app-logo {
  width: 20px; 
  height: auto; 
  margin-right: 10px; 
}

.form-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  align-items: center;
  z-index: 3;  
  height: 100vh;
  color: white; 
}

label {
color: white; 
}

form input {
  display: block;
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 2px solid #9a92d7;
  border-radius: 4px;
  box-sizing: border-box;
}


input:hover {
box-shadow: 0 4px 6px rgba(154, 228, 235, 0.818);
animation: border-color-change 4s infinite;
}

@keyframes border-color-change {
0% { border-color: #9a92d7; }
50% { border-color: rgb(213, 243, 251); }
100% { border-color: #9a92d7; }
}

.offscreen {
    position: absolute;
    left: -9999px;
}

.span {
  padding: 5px 10px;
  background-color: transparent; 
  color: #fff; 
  border: 1px solid white; 
  border-radius: 5px; 
  cursor: pointer; 
  margin-right: 10px; 
}

.span-container .active {
  background-color: white !important;
  color: black; 
}

textarea {
  width: 100%; 
}